.btn {
    border: 1px solid #ccc;
    display: inline-block;
    padding: 8px 16px;
    vertical-align: middle;
    overflow: hidden;
    text-decoration: none;    
    text-align: center;
    cursor: pointer;
    white-space: nowrap;
    color: #000;
    background-color: #fff;
}
.btn:hover, .btn:active{
  color: #000;
  background-color: #ccc;
}

.source-container {   
