/*Слайдеры*/
/*Chrome*/
input[type='range'] {
  overflow: hidden;
  width: 90px;
  -webkit-appearance: none;  
  border: 0.25px solid #000;
}

input[type='range']::-webkit-slider-runnable-track {
  height: 10px;
  -webkit-appearance: none;  
  background: #f2f2f2;  
  margin-top: -1px;
}

input[type='range']::-webkit-slider-thumb {
  width: 10px;
  -webkit-appearance: none;
  height: 10px;
  cursor: ew-resize;
  background: #272822; 
  box-shadow: -80px 0 0 80px #6c757d;
}

/** FF*/
input[type="range"]::-moz-range-progress {
  background-color: #6c757d; 
}
input[type="range"]::-moz-range-track {  
  background-color: #f2f2f2;
}
/* IE*/
input[type="range"]::-ms-fill-lower {
  background-color: #6c757d; 
}
input[type="range"]::-ms-fill-upper {  
  background-color:  #f2f2f2;
}
/*окончание стилей для слайдеров*/
/* оформляем label */
.switch {
	position: relative;
	display: inline-block;
	width: 40px;
	height: 20px;
}
/*прячем флаг*/
.switch input{
	display: none;

}
/*оформляем спан*/
.switch-slider {
	position: absolute;
	cursor: pointer;
	top:0;
	right: 0;
	bottom: 0;
	left: 0;
	background: #f2f2f2;
	transition: .4s;
  border-radius: 17px;  
	border: 0.25px solid #272822;
}
/*имитация переключателя*/
.switch-slider:before{
	position: absolute;
	content: '';
	height: 18px;
	width: 20px;
	left: 0px;
	/*bottom: 2px;*/
	background: #272822;
	transition:  .4s;
  border-radius: 50%;
}

/*оформляем спан когда флаг выбран*/
input:checked + .switch-slider{
	background: #6c757d;
}

/*анимация движения переключателя*/
input:checked + .switch-slider:before{
	-webkit-transform: translateX(18px);
	        transform: translateX(18px);
}
